footer.footer {
    width: calc( 100% - 230px );
    margin-left: 230px;
    @include r($desktop) {
        margin-left: 0;
        width: 100%; }
    h3 {
        text-transform: uppercase;
        margin-bottom: 58px; }
    .inner-container-for-contact {
        display: flex;
        justify-content: space-between;
        @include r(1380) {
            flex-wrap: wrap;
            justify-content: space-around; } }
    .con_item {
        width: 25%;
        display: flex;
        align-items: center;
        @include r(1380) {
            width: 280px;
            margin-bottom: 30px; }
        a, address {
            font-style: normal;
            color: #000000;
            font-family: 'Roboto';
            font-size: 16px;
            font-weight: 500;
            line-height: 18px; }
        address {
            max-width: 180px; } }
    .wrapper-link {
        display: flex;
        flex-direction: column; }
    .inner-container-for-soc {
        margin-top: 70px;
        display: flex;
        justify-content: space-between;
        @include r(1380) {
            flex-wrap: wrap;
            justify-content: space-around; }
        @include r($tablet) {
            margin-top: 20px; } }
    a.soc_item {
        display: block;
        width: calc( 100% / 3 - 20px );
        text-align: center;
        text-transform: uppercase;
        padding: 15px 0;
        color: #ffffff;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 500;
        text-transform: uppercase;
        border-radius: 5px;
        @include r(1380) {
            width: 280px;
            margin-bottom: 30px; } }
    a.soc_item:nth-child(1) {
        background: linear-gradient(45deg, #f09433 0%,#e6683c 25%,#dc2743 50%,#cc2366 75%,#bc1888 100%);
 }        // background: radial-gradient(circle at 33% 100%, #fed373 4%, #f15245 30%, #d92e7f 62%, #9b36b7 85%, #515ecf)
    a.soc_item:nth-child(2) {
        background-color: #5181b8; }
    a.soc_item:nth-child(3) {
        background-color: #f58220; }
    h6.copy {
        margin: 30px 0 30px 0;
        text-align: center;
        color: #000000;
        font-family: 'Roboto';
        font-size: 16px;
        font-weight: 500;
        line-height: 22px;
        letter-spacing: 0.05px;
        text-transform: uppercase; }
    svg {
        width: 33px;
        height: 32px;
        fill: #e31e24;
        margin-right: 10px; } }
