* {
  box-sizing: border-box;
  outline: none;
}

body, html {
  background: white;
  color: #333;
  font-family: "SegoeUI", sans-serif;
  font-size: 16px;
  min-width: 320px;
  max-width: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.page__content {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.container {
  max-width: 1100px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media only screen and (max-width: 1370px) {
  .container {
    padding: 0 15px;
  }
}

@media only screen and (max-width: 1200px) {
  .container {
    max-width: 970px;
  }
}

@media only screen and (max-width: 992px) {
  .container {
    max-width: 740px;
  }
}

@media only screen and (max-width: 768px) {
  .container {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.dev-bp-debug {
  background: black;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9999;
  color: white;
  padding: 5px;
  font-size: 12px;
  border-bottom-left-radius: 5px;
}

h1, h2, h3, h4, h5, p, a, span, ul, ol, li {
  padding: 0;
