// buttons
button {
  -webkit-apperance: none;
  border: 0;
  box-shadow: none;
  cursor: pointer;
  &:focus {
    outline: none; } }

.btn {
  display: block;
  padding: 19px 0 17px 0;
  border-radius: 5px;
  background-color: #e31e24;
  color: #ffffff;
  font-size: 20px;
  font-weight: 600;
  transition: 0.3s;
  cursor: pointer;
  &:hover {
    background-color: #ca0b11; } }
label.btn {
  cursor: pointer;
  input {
    color: #ffffff;
    font-family: $font-family;
    font-size: 14px;
    font-weight: 500;
    line-height: 25px;
    text-transform: uppercase;
    letter-spacing: 0.7px;
    cursor: pointer;
    border: none;
    background: none;
    margin: 0!important; }
  &:hover input {
    color: #ff9d01; }
  &:after {
    position: absolute;
    right: 20px;
    top: 18px; } }
