.homepage #back-top {
    display: none; }
.homepage footer.footer {
    display: none; }
.page__content {
    align-items: flex-end;
    @include r($desktop) {
        align-items: center; } }
section.first-screen {
    height: 100vh;
    width: calc( 100% - 230px );
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-position: center;
    @include r($desktop) {
        width: 100%; }
    @include r($tablet) {
        background-position: top center; }
    h1 {
        color: #e31e24;
        font-size: 48px;
        font-weight: 700;
        margin-bottom: 20px;
        @include r($wide) {
            font-size: 40px; }
        @include r($desktop) {
            font-size: 35px; } }
    h6 {
        margin: 0;
        color: #e31e24;
        font-size: 40px;
        font-weight: 700;
        margin-bottom: 38px;
        @include r($wide) {
            font-size: 35px; }
        @include r($desktop) {
            font-size: 30px; }
        @include r($tablet) {
            font-size: 26px; }
        a {
            color: #e31e24;
            font-size: 40px;
            font-weight: 700;
            @include r($wide) {
                font-size: 35px; }
            @include r($desktop) {
                font-size: 30px; }
            @include r($tablet) {
                font-size: 24px; }
            @include r($mobile) {
                display: block; } } }
    a.btn {
        width: 232px;
        padding: 15px 0 16px 0;
        margin: 0 auto; } }
.conditioners-page section.first-screen a.btn {
    width: 282px; }
section.application-for-free-measurement, section.steps-for-successful, section.what-we-do, section.why-we, section.our-clients, section.portfolio {
    padding: 79px 0;
    width: calc( 100% - 230px );
    @include r($desktop) {
        width: 100%; }
    @include r($tablet) {
        padding: 50px 0; } }
section.application-for-free-measurement {
    border-bottom: 1px solid #e31e24;
    form {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
        @include r($wide) {
            flex-wrap: wrap; }
        @include r($tablet) {
            justify-content: space-around; }
        @include r($mobile) {
            flex-direction: column;
            align-items: center; } }
    .ui-group {
        width: calc( 100% / 3 - 15px );
        margin-bottom: 0;
        @include r($tablet) {
            width: calc( 50% - 20px );
            margin-bottom: 30px; }
        @include r($mobile) {
            width: calc( 100% - 20px ); } }
    .ui-group:last-child {
            margin-bottom: 0; }
    .ui-group input[type="text"], .ui-group input[type="tel"] {
        border: none;
        background: transparent;
        border-radius: 0;
        padding-left: 0;
        border-bottom: 1px solid #000000;
        color: #000000;
        font-size: 18px;
        font-weight: 400;
        line-height: 20px; }
    .ui-group input[type="submit"] {
        border: none;
        padding: 16px 0; }
    h4 {
        margin-top: 40px;
        color: #000000;
        text-transform: uppercase;
        font-family: "Segoe UI";
        font-size: 29px;
        font-weight: 700;
        text-align: center;
        margin-bottom: 32px;
        @include r($tablet) {
            font-size: 25px;
            margin-bottom: 20px; }
        @include r($mobile) {
            font-size: 22px;
            margin-top: 20px; } }
    a.tel {
        display: inline-block;
        margin-left: 50%;
        transform: translateX(-50%);
        font-size: 48px;
        font-weight: 700;
        color: #e31e24;
        @include r($wide) {
            font-size: 38px; }
        @include r($desktop) {
            font-size: 30px; }
        @include r($tablet) {
            font-size: 24px; }
        @include r($mobile) {
            margin-left: 15%;
            transform: translateX(0); } }
    p {
        color: #000000;
        font-size: 15px;
        font-weight: 400;
        letter-spacing: 0.21px;
        margin-top: 20px;
        @include r($wide) {
            text-align: center; } }
    a.pol-cong {
        letter-spacing: 0.21px;
        text-decoration: underline;
        color: #000000;
        @include r($mobile) {
            display: block; } }
    span {
        text-transform: uppercase; } }
section.steps-for-successful {
    .wrapper_items {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around; }
    .steps-success {
        max-width: 538px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 96px;
        text-align: center;
        position: relative;
        @include r($tablet) {
            margin-bottom: 50px; }
        @include r($mobile) {
            margin-bottom: 30px; } }
    .wrapper_icon {
        margin-bottom: 60px; }
    svg {
        fill: #e31e24; }
    .steps-success:nth-child(1) {
        svg {
            width: 71px;
            height: 70px; } }
    .steps-success:nth-child(2) {
        svg {
            width: 90px;
            height: 71px; } }
    .steps-success:nth-child(3) {
        margin-bottom: 0;
        @include r(1380) {
            margin-bottom: 96px; }
        @include r($tablet) {
            margin-bottom: 50px; }
        @include r($mobile) {
            margin-bottom: 30px; }
        svg {
            width: 71px;
            height: 71px; } }
    .steps-success:nth-child(4) {
        margin-bottom: 0;
        svg {
            width: 71px;
            height: 71px; }
        h4 {
            line-height: 25px; } }
    h4 {
        color: #000000;
        font-size: 20px;
        font-weight: 600;
        margin-bottom: 20px; }
    p {
        color: #000000;
        font-size: 16px;
        font-weight: 400;
        span {
            font-weight: 700; } }
    .steps-success > span {
        left: calc( 50% + 30px );
        top: 25px;
        display: block;
        opacity: 0.15;
        position: absolute;
        color: #000000;
        font-size: 80px;
        font-weight: 900;
        text-transform: uppercase; } }
section.what-we-do {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    min-height: 686px;
    padding-bottom: 34px;
    h3 {
        color: #fff; }
    .wwd-items {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap; }
    .wwd-item {
        max-width: 347px;
        min-width: 272px;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        margin-bottom: 45px; }
    p {
        color: #ffffff;
        font-size: 20px;
        font-weight: 600;
        line-height: 24px; }
    .wrapper_icon {
        width: 145px;
        height: 145px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #ffffff;
        margin-bottom: 25px; }
    svg {
        width: 61px;
        height: 61px;
        fill: #e31e24; } }
.conditioners-page section.what-we-do {
    @include r($wide) {
        background-size: 400%;
        background-position: right top; }
    @include r($desktop) {
        background-size: 500%; }
    @include r($tablet) {
        background-size: 1200%;
        background-position: right -500px top; }
    @include r($mobile) {
        background-size: 1500%; } }
.ventilation-page section.what-we-do .wwd-item {
    max-width: 319px;
    min-width: 260px; }
.ventilation-page section.what-we-do .wwd-item:nth-child(5) {
    margin-left: 40px;
    @include r(1370) {
        margin-left: 0; } }
section.why-we {
    border-bottom: 1px solid #e31e24;
    p {
        color: #000000;
        font-size: 16px;
        font-weight: 600;
        line-height: 22px;
        margin-top: 30px;
        &:first-of-type {
            margin-top: 0; } }
    p.p-red-block {
        color: #ffffff;
        // font-family: "Segoe UI - Semibold Italic"
        font-size: 16px;
        font-style: italic;
        font-weight: 500;
        line-height: 22px;
        background-color: #e31e24;
        padding: 25px 0;
        text-align: center; }
    .wrapper-img {
        margin-top: 76px;
        max-width: 1099px;
        position: relative;
        @include r($wide) {
            display: flex;
            flex-direction: column;
            align-items: center; } }
    .wrapper-img > img {
        width: 100%;
        height: auto;
        @include r($wide) {
            margin-bottom: 30px; } }
    .special-offer-block-1 {
        position: absolute;
        top: 20px;
        left: 20px;
        width: 377px;
        height: 161px;
        background-color: #e31e24;
        @include r(1330) {
            height: 100px; }
        @include r($wide) {
            position: static;
            margin-bottom: 15px; }
        @include r($mobile) {
            width: 100%;
            height: 93px; }
        img {
            float: left;
            margin-top: 15px;
            margin-left: 13px;
            margin-right: 15px; } }
    .special-offer-block-1-txt {
        margin-top: 24px;
        p {
            color: #ffffff;
            font-size: 24px;
            font-weight: 600;
            line-height: 30px;
            @include r($mobile) {
                font-size: 16px; } } }
    p.special-offer-block-2 {
        color: #e31e24;
        font-size: 18px;
        font-weight: 600;
        line-height: 24px;
        max-width: 365px;
        position: absolute;
        top: 20px;
        right: 192px;
        text-align: center;
        @include r(1330) {
            right: 58px; }
        @include r($wide) {
            position: static;
            margin-bottom: 15px; } }
    .special-offer-block-3 {
        position: absolute;
        bottom: 19px;
        right: 58px;
        max-width: 631px;
        text-align: center;
        @include r($wide) {
            position: static; }
        p:first-child {
            color: #000000;
            font-size: 26px;
            font-weight: 600;
            line-height: 30px;
            margin-bottom: 12px;
            @include r($mobile) {
                font-size: 20px; } }
        p:last-child {
            color: #010101;
            font-size: 20px;
            font-weight: 600;
            margin-top: 0; } } }
.conditioners-page section.why-we {
    .special-offer-block-1 {
        height: 108px; }
    p.special-offer-block-2 {
        right: 13px;
        top: 20px;
        font-weight: 700;
        @include r($wide) {
            max-width: 420px; }
        span {
            display: block;
            @include r($wide) {
                display: inline; } } }
    .special-offer-block-3 {
        max-width: 660px;
        p {
            color: #fff;
            @include r($wide) {
                color: #000; } } } }
.ventilation-page section.why-we {
    .special-offer-block-1 {
        background-color: transparent;
        top: 20px;
        left: 0;
        height: auto;
        @include r($wide) {
            background: #e31e24;
            position: absolute;
            left: 50%;
            transform: translateX(-50%); }
        @include r($mobile) {
            transform: translateX(-50%) translateY(-50%);
            top: 50%;
            bottom: auto; } }
    .special-offer-block-1-txt {
        @include r($wide) {
            margin-top: 0; } }
    .special-offer-block-1-txt p {
        text-align: center;
        @include r(1370) {
            font-size: 17px; } }
    .special-offer-block-3 {
        text-align: left;
        max-width: 382px;
        left: 16px;
        color: #fff;
        bottom: 78px;
        right: auto;
        @include r(1370) {
            bottom: 11px;
            max-width: 334px; }
        @include r($wide) {
            position: absolute;
            background: #e31e24;
            top: 50%;
            transform: translateY(-50%) translateX(-50%);
            padding: 15px;
            right: auto;
            bottom: auto;
            left: 50%;
            max-width: 370px; }
        @include r($tablet) {
            text-align: center;
            padding: 5px; }
        @include r($mobile) {
            position: static;
            margin: 0;
            transform: none; }
        p {
            color: #fff; }
        p:last-child {
            font-size: 20px;
            @include r(1370) {
                font-size: 18px; }
            @include r($tablet) {
                font-size: 16px; } }
        p:first-child {
            @include r(1370) {
                font-size: 25px; }
            @include r($tablet) {
                font-size: 18px; } } }
    img {
        margin-right: 0;
        @include r($wide) {
            display: none; } }
    img[alt="special-offer-img2mob"] {
        display: none;
        @include r($wide) {
            display: block; } } }
section.our-clients {
    position: relative;
    h3 {
        text-transform: uppercase;
        @include r($mobile) {
            margin-bottom: 24px; } }
    .swiper-slide {
        display: flex;
        align-items: center;
        height: 130px;
        justify-content: center; }
    img {
        width: 100%;
        max-width: none;
        height: 100%;
        object-fit: contain; }
    a {
        display: block;
        width: 100%;
        height: 100%; }
    .our-clients-button-next, .our-clients-button-prev {
        background-image: none;
        top: 56%;
        display: flex;
        justify-content: center;
        align-items: center;
        @include r($mobile) {
            top: 61%; }
        svg {
            width: 16px;
            height: 30px;
            fill: #e31e24;
            @include r($tablet) {
                width: 10px;
                height: 20px; }
            @include r($mobile) {
                width: 6;
                height: 14px; } } }
    .our-clients-button-next {
        right: 51px;
        @include r(1500) {
            right: 15px; }
        @include r($mobile) {
            right: 5px; } }
    .our-clients-button-prev {
        left: 51px;
        @include r(1500) {
            left: 15px; }
        @include r($mobile) {
            left: 5px; } } }
section.portfolio {
    margin-bottom: 80px;
    padding-top: 0;
    padding-bottom: 0;
    h3 {
        text-align: center; }
    .fotorama {
        display: flex;
        justify-content: center; } }
.conditioners-page section.portfolio {
    .fotorama {} }
section.main-slider {
    max-width: calc( 100% - 230px );
    width: 100%;
    height: 100vh;
    @include r($desktop) {
        max-width: 100%;
        height: calc( 100vh - 266px ); }
    @include r($mobile) {
        height: calc( 100vh - 236px ); }
    .swiper-container.main-slider {
        height: 100%; }
    .swiper-slide {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover; }
    h2 {
        color: #000201;
        font-size: 48px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 37px;
        @include r($desktop) {
            font-size: 40px;
            margin-bottom: 33px; }
        @include r($tablet) {
            font-size: 35px; }
        @include r($mobile) {
            font-size: 25px;
            margin-bottom: 12px;
            max-width: 250px;
            margin-right: auto;
            margin-left: auto;
            text-align: center; } }
    a.btn {
        width: 232px;
        text-align: center;
        @include r($desktop) {
            padding: 8px 0 10px 0; }
        @include r($mobile) {
            width: 190px; } }
    .main-slider-button-next, .main-slider-button-prev {
        background-image: none;
        width: 60px;
        height: 60px;
        background-color: rgba(255,255,255,0.9);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        @include r($tablet) {
            width: 40px;
            height: 40px; }
        @include r($mobile) {
            top: 50%;
            width: 25px;
            height: 25px; }
        svg {
            width: 16px;
            height: 30px;
            fill: #e31e24;
            @include r($tablet) {
                width: 10px;
                height: 20px; }
            @include r($mobile) {
                width: 6;
                height: 14px; } } }
    .main-slider-button-next {
        right: 30px;
        @include r($mobile) {
            right: 5px; } }
    .main-slider-button-prev {
        left: 30px;
        @include r($mobile) {
            left: 5px; } }
    .main-slider-swiper-pagination {
        bottom: 43px;
        .swiper-pagination-bullet {
            margin: 0;
            margin-left: 10px;
            width: 11px;
            height: 11px;
            background-color: #ffffff;
            border: none;
            &:first-of-type {
                margin-left: 0; } }
        .swiper-pagination-bullet-active {
            background-color: #e31e24;
            border: none;
            &::after {
                display: none; } } } }
.pagination-page {
    position: fixed;
    top: 50%;
    right: 209px;
    transform: translateY(-50%);
    z-index: 10;
    @include r(1850) {
        right: 100px; }
    @include r(1700) {
        right: 20px; }
    .dots.active {
        background-color: #e31e24; }
    .dots {
        display: block;
        width: 11px;
        height: 11px;
        margin-bottom: 9px;
        background-color: #a9a9a9;
        &:last-child {
            margin-bottom: 0; } } }
