header.header {
    // height: 100vh
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    @include r($desktop) {
        flex-direction: column;
        justify-content: flex-start;
        align-items: center; }
    .container-for-nav {
        padding: 30px;
        max-width: 230px;
        min-width: 230px;
        width: 100%;
        background-color: #fff;
        position: fixed;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 10;
        @include r($desktop) {
            height: auto;
            position: static;
            max-width: 700px;
            min-width: none;
            padding: 0;
            padding-top: 20px; }
        @include r($mobile) {
            padding-top: 10px; } }
    a.wrapper-logo {
        display: block;
        width: 170px;
        text-align: center;
        padding-bottom: 29px;
        border-bottom: 1px solid #e31e24;
        margin-bottom: 65px;
        @include r($desktop) {
            margin-left: auto;
            margin-right: auto;
            padding-bottom: 15px;
            margin-bottom: 10px; }
        @include r($mobile) {
            padding-bottom: 10px; }
        img {
            width: 100%;
            height: auto;
            max-width: none;
            @include r($desktop) {
                display: block; } } }
    .wrapper-nav-and-call-us {
        @include r($desktop) {
            display: flex;
            flex-direction: column-reverse; } }
    nav.nav-header {
        padding-bottom: 54px;
        border-bottom: 1px solid #e31e24;
        margin-bottom: 25px;
        @include r($desktop) {
            background: #000;
            padding-bottom: 0;
            border-bottom: none;
            padding: 10px 0 10px 10px;
            border-radius: 5px;
            cursor: pointer;
            background-color: rgba(0,0,0,.05);
            position: relative; }
        @include r($tablet) {
            margin-right: 15px;
            margin-left: 15px; }
        @include r($mobile) {
            margin-bottom: 10px; }
        p {
            display: none;
            @include r($desktop) {
                display: block; } }
        .burger {
            padding: 5px;
            width: 35px;
            height: 25px;
            position: absolute;
            right: 15px;
            top: 50%;
            cursor: pointer;
            transform: translateY(-50%);
            display: none;
            flex-direction: column;
            justify-content: space-between;
            @include r($desktop) {
                display: flex; } }
        .icon-bar-b {
            width: 100%;
            height: 1px;
            background-color: #e31e24;
            border-radius: 15px; }
        ul {
            @include r($desktop) {
                position: absolute;
                display: none;
                background-color: rgba(239,239, 239,1);
                padding: 10px 0 10px 10px;
                border-radius: 5px;
                width: 100%;
                left: 0;
                top: calc( 100% + 10px );
                margin: 0;
                padding: 0;
                padding-top: 15px;
                z-index: 2; } }
        li {
            margin-bottom: 22px;
            @include r($desktop) {
                border-bottom: 1px solid rgba(0,0,0,.1);
                padding-left: 30px;
                margin-left: 0;
                margin-bottom: 10px;
                padding-bottom: 8px; }
            &:last-child {
                margin-bottom: 0;
                @include r($desktop) {
                    border-bottom: none; } } }
        a {
            color: #000000;
            font-family: "SegoeUI";
            font-size: 18px;
            font-weight: 400;
            transition: 0.3s;
            &:hover {
                color: #e31e24; } } }
    .wrapper-call-us {
        padding-bottom: 29px;
        border-bottom: 1px solid #e31e24;
        @include r($desktop) {
            text-align: center;
            padding-bottom: 15px;
            border-bottom: none; }
        p {
            margin-bottom: 10px;
            font-size: 20px;
            font-weight: 400;
            color: #000000; }
        a {
            display: block;
            color: #e31e24;
            font-family: "SegoeUI";
            font-size: 20px;
            font-weight: 400;
            letter-spacing: -0.1px; } }
